<template>
  <!-- 角色管理 -->
  <el-container class="permission-assignment">
    <el-aside width='20%'>
      <!--<el-scrollbar style='height: 100%;'>-->
        <org-tree ref="tree" :treeData="treeData" :expandAll="expandAll" @node-click="orgTreeClick"></org-tree>
      <!--</el-scrollbar>-->
    </el-aside>
    <el-main>
      <el-form :inline="true">
        <el-form-item>
          <el-input size="small" placeholder="请输入角色名称" v-model="keyWordSearch" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small" @click="getDataList()">检索</el-button>
          <el-button type="primary" size="small" @click="addOrUpdateHandle()">添加</el-button>
        </el-form-item>
      </el-form>
      <el-table
        :data="dataList"
        border
        v-loading="dataListLoading"
        :height="siteContentViewHeight - 105+'px'"
        style="width: 100%;">
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column prop="roleCode" width="200" label="角色代码"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleType" width="200" label="角色类别">
          <template slot-scope="scope">
            <div v-if="parseInt(scope.row.roleType)===1">系统管理员</div>
            <div v-if="parseInt(scope.row.roleType)===2">安全保密员</div>
            <div v-if="parseInt(scope.row.roleType)===3">安全审计员</div>
            <div v-if="parseInt(scope.row.roleType)===4">业务管理员</div>
          </template>
        </el-table-column>
        <el-table-column prop="roleState" width="120" label="角色状态">
          <template slot-scope="scope">
            <el-tag v-if="parseInt(scope.row.roleState)===0" size="small" type="danger">停用</el-tag>
            <el-tag v-else size="small">启用</el-tag>
          </template>
        </el-table-column>
        <el-table-column width="200" label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" plain @click="addOrUpdateHandle(scope.row)">修改</el-button>
            <el-button type="danger" size="mini" plain @click="deleteHandle(scope.$index,scope.row)">删除</el-button>
            <el-button type="success" size="mini" plain @click="authorizationHandle(scope.row)">授权</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--分页-->
      <pagination
        :total="page.total"
        :pageSize="page.pageSize"
        @PageTurning="PageTurning">
      </pagination>
      <!-- 弹窗, 新增 / 修改 -->
      <add-or-update v-if="addOrUpdateVisible" ref="addOrUpdate" @refreshDataList="getDataList"></add-or-update>
      <!-- 弹窗，授权 -->
      <grant-authorization v-if="grantAuthorizationVisible" ref='grantAuthorization'></grant-authorization>
    </el-main>
  </el-container>
</template>

<script>
  import AddOrUpdate from './add-or-update'
  import GrantAuthorization from './grant-authorization.vue'
  import orgTree from "@/components/baseComponents/orgTree.vue";
  import pagination from '@/components/baseComponents/Pagination.vue'
  export default{
    props:['siteContentViewHeight'],
    data(){
      return{
        treeData:[],
        expandAll:true,
        sysId:'',
        keyWordSearch:'',
        dataList:[],
        dataListLoading:false,
        dataListSelections:[],
        addOrUpdateVisible:false,
        grantAuthorizationVisible:false,
        page: {
          pageIndex:this.API.constObj.pageIndex,//当前页码
          pageSize:this.API.constObj.pageSize,//每页大小
          total: 0//总条数
        }
      }
    },
    components:{
      AddOrUpdate,GrantAuthorization,pagination,orgTree
    },
    created () {
      this.getDataTree()
    },
    methods:{
      getDataTree(){//获取区域节点
        this.$http({
          url: this.$http.adornUrl(this.API.getRoleMenuTree),
          method: 'post',
          data: this.$http.adornData({
            areaId:this.$store.state.user.areaId
          })
        }).then(({data}) => {
          if(data && data.code===0){
            var arr=[];
            if(data.data.length>0){
              data.data.forEach(item=>{
                arr.push({name:item.sysName,id:item.sysId})
              })
              this.treeData=[{name:'应用子系统',children:arr}];
              this.orgTreeClick(this.treeData[0].children[0])
            }
          }else{
            this.$message.error(data.msg);
            this.treeData=[];
          }
        });
      },
      //机构树点击事件
      orgTreeClick(data) {
        this.sysId=data.id;
        this.getDataList();
      },
      getDataList () {// 获取数据列表
        this.dataListLoading = true
        this.$http({
          url: this.$http.adornUrl(this.API.getRoleInfo),
          method: 'post',
          data: this.$http.adornData({
            sysId:this.sysId, //子系统ID
            areaId:this.$store.state.user.areaId,//区域ID
            current:this.page.pageIndex,
            pageSize:this.page.pageSize,
            roleName:this.keyWordSearch
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.dataList=data.data.records;
            this.page.total=data.data.total;
          }else{
            this.$message.error(data.msg);
            this.dataList=[];
            this.page.total=0;
          }
          this.dataListLoading = false
        });
      },
      addOrUpdateHandle(row){// 新增 / 修改
        if(this.sysId==''){
          this.$confirm('请选择一个节点', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
          return;
        }
        this.addOrUpdateVisible = true;
        this.$nextTick(() => {
          this.$refs.addOrUpdate.init(row,this.sysId)
        })
      },
      deleteHandle(index,row){//删除
        this.$confirm('确定要删除该信息？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.$http.adornUrl(this.API.deleteRoleInfo),
            method: 'post',
            data: this.$http.adornData({
              roleId:row.roleId,
              sysId: this.sysId, //子系统ID
              areaId:this.$store.state.user.areaId,//区域ID
              userId:this.$store.state.user.id
            })
          }).then(({data}) => {
            if(data && data.code===0){
              this.dataList.splice(index,1);
              this.$message.success('操作成功');
            }else{
              this.$message.error(data.msg);
            }
          });
        }).catch(() => {})
      },
      authorizationHandle(row){//授权弹窗
        this.grantAuthorizationVisible=true;
        this.$nextTick(() => {
          this.$refs.grantAuthorization.init(row,this.sysId)
        })
      },
      //分页组件回调方法
      PageTurning(page_obj) {
      	this.page.pageIndex=page_obj.page_index;
      	this.page.pageSize=page_obj.page_size;
      	this.getDataList();
      }
    }
  }
</script>

<style lang="scss" scoped>
  .permission-assignment{
    .el-main{padding-bottom: 0;padding-top: 0;}
    .el-form--inline{padding-left: 10px;}
    .el-form--inline .el-form-item{margin-bottom: 10px;margin-top: 10px;}
    .el-aside {padding-top: 10px; border-right: 1px solid #e6e6e6;}
    .el-table--border{border-left: none;}
  }
</style>



