<template>
  <el-dialog
    :title="!dataForm.id ? '角色新增' : '角色修改'"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="100px">
      <el-form-item label="角色代码" prop="roleCode">
        <el-input v-model="dataForm.roleCode"></el-input>
      </el-form-item>
      <el-form-item label="角色名称" prop="roleName">
        <el-input v-model="dataForm.roleName"></el-input>
      </el-form-item>
      <el-form-item label="角色类别" prop="roleType">
        <el-radio-group v-model="dataForm.roleType">
          <el-radio v-for="item in roleType" :label="item.id" :key='item.id'>{{item.text}}</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="角色状态" prop="roleState">
        <el-radio-group v-model="dataForm.roleState">
          <el-radio :label="0">停用</el-radio>
          <el-radio :label="1">启用</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        sysId:'',
        dataForm: {
          roleId:0,
          roleCode:'',//角色代码
          roleName:'',//角色名称
          roleType:1,//角色类型
          roleState:1//角色状态,默认传1
        },
        dataRule: {
          roleCode: [
            { required: true, message: '角色代码必填', trigger: 'blur' }
          ],
          roleName: [
            { required: true, message: '角色名称必填', trigger: 'blur' }
          ]
        },
        roleType:[
          {id:1,text:'系统管理员'},
          {id:2,text:'安全保密员'},
          {id:3,text:'安全审计员'},
          {id:4,text:'业务管理员'}
        ]
      }
    },
    methods: {
      init(row,sysId) {
        this.dataForm.roleId=row && row.roleId || 0
        this.visible = true;
        this.sysId=sysId;
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          if(this.dataForm.roleId) {
            this.dataForm.roleCode=row.roleCode
            this.dataForm.roleName=row.roleName
            this.dataForm.roleType=row.roleType?parseInt(row.roleType):1
            this.dataForm.roleState=row.roleState?parseInt(row.roleState):1
          }
        })
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            var params={
              sysId:this.sysId, //子系统ID
              areaId:this.$store.state.user.areaId//区域ID
            }
            this.dataForm.roleId=this.dataForm.roleId || undefined
            Object.assign(params,this.dataForm);
            this.$http({
              url: this.$http.adornUrl(this.API.savaOrUpdateRoleInfo),
              method: 'post',
              data: this.$http.adornData(params)
            }).then(({data}) => {
              if(data && data.code===0){
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              }else{
                this.$message.error(data.msg);
              }
            });
          }
        })
      }
    }
  }
</script>



