<template>
  <el-dialog
    class="grant-authorization"
    :title="title"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <el-tabs v-model="activeName" type="card">
      <el-tab-pane label="功能菜单" name="first">
        <div class="float-right">
          <el-checkbox v-model="checked">关联子节点</el-checkbox>
          <el-button type="primary" size="small" @click="getCurrentKey">保存</el-button>
        </div>
        <div class="clearfix"></div>
        <el-scrollbar style='height: 400px;'>
          <org-tree ref="tree"
            :treeData="treeData"
            :checkbox='treeCheckBox'
            :checkStrictly='!checked'
            :defaultChecked='defaultChecked'
          ></org-tree>
        </el-scrollbar>
      </el-tab-pane>
    </el-tabs>
  </el-dialog>
</template>

<script>
  import orgTree from "@/components/baseComponents/orgTree.vue";
  export default{
    components: {
      orgTree
    },
    data(){
      return{
        title:'',
        roleId:'',
        visible:false,
        activeName:'first',
        checked:false,
        treeData:[],
        treeCheckBox:true,//tree复选框是否有
        defaultChecked:[]
      }
    },
    methods:{
      init(row,sysId){
        this.visible = true;
        this.title=row.roleName;
        this.roleId=row.roleId;
        this.$http({
          url: this.$http.adornUrl(this.API.getSysMenuTree),
          method: 'post',
          data: this.$http.adornData({
            areaId:this.$store.state.user.areaId,
            sysId:sysId,//子系统ID
            roleId:row.roleId
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.treeData=data.data;
            this.defaultChecked=this.getIsCheckCurrent(data.data);
          }else{
            this.$message.error(data.msg);
            this.treeData=[];
            this.defaultChecked=[];
          }
        });
      },
      getCurrentKey(){//保存选中节点
        this.$http({
          url: this.$http.adornUrl(this.API.savaOrUpdateSysMenuTree),
          method: 'post',
          data: this.$http.adornData({
            roleId:this.roleId,
            ids:this.$refs.tree.getCheckedKeys().join(',')
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message.success('操作成功');
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      getIsCheckCurrent(arr){//多层树结构中取出是否选中
        const setCheckedKeys=[];
        if(!arr) return;
        const getChild=(arr)=>{
          arr.forEach(item=>{
            if(item.isCheck) setCheckedKeys.push(item.id);
            if(item['children'] && item['children'].length>0){
              getChild(item.children)
            }
          })
          return setCheckedKeys;
        }
        return getChild(arr)
      },

    }
  }
</script>

<style lang="scss" scoped>
  .float-right{
    padding-bottom: 10px;
    .el-checkbox{padding-right: 20px;}
  }
</style>



